.heart{
  color: linear-gradient(to top, #151a23 0%, #4b5e80 100%);
  text-align: center;
  -webkit-user-select:none;
      -ms-user-select:none;
          user-select:none;
  position: relative;
  top:4px;
}
.ripple{
  position:absolute;
  width: 24px;
  height: 24px;
  background-color: red;
  border-radius:50% ;
  transform: scale(0);
}

.like-pelicula [type="checkbox"]:checked ~ .heart{
  animation-name: heart;
  animation-duration: .6s;
  animation-fill-mode: forwards;
}
.like-pelicula [type="checkbox"]:checked ~ .ripple {
  animation-name: ripple;
  animation-duration: .6s;
  animation-fill-mode: forwards;
}

@keyframes heart {

  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
    color: red;
  }
}
@keyframes ripple {

  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
    opacity:0;
  }
}

