.social-networks.spin-icon .a-social:hover:after, .social-networks.spin-icon .a-social:focus:after {
	transition: transform 0.4s ease-out;
	transform: rotateZ(360deg);
}
.social-networks.square .a-social:hover:before, .social-networks.square .a-social:focus:before {
