	  box-shadow: 0 0 0 0.2rem rgba(252, 186, 3, .45);
	  transform: translateY(4px);
  }
  .add-to-cart-button:hover {
	  cursor: pointer;
  }
  .add-to-cart-button:hover, .add-to-cart-button:focus {
	  box-shadow: 0 0 0 0.2rem rgba(252, 186, 3, .45);
	  transform: translateY(-1px);
  }
  .add-to-cart-button.added {
	  background: #2fbf30;
	  box-shadow: 0 0 0 0.2rem rgba(11, 252, 3, 0.45);
  }
  .add-to-cart-button.added .add-to-cart {
	  display: none;
  }
  .add-to-cart-button.added .added-to-cart {
	  display: block;
  }
  .add-to-cart-button.added .cart-icon {
	  animation: drop 0.3s forwards;
	  -webkit-animation: drop 0.3s forwards;
	  animation-delay: 0.18s;
  }
  .add-to-cart-button.added .box-1, .add-to-cart-button.added .box-2 {
	  top: 18px;
  }
  .add-to-cart-button.added .tick {
	  animation: grow 0.6s forwards;
	  -webkit-animation: grow 0.6s forwards;
	  animation-delay: 0.7s;
  }
  .add-to-cart, .added-to-cart {
	  margin-left: 36px;
  }
  .added-to-cart {
	  display: none;
	  position: relative;
  }
  .add-to-cart-box {
	  height: 5px;
	  position: absolute;
	  top: 0;
	  width: 5px;
  }
  .box-1, .box-2 {
	  transition: 0.4s ease;
	  top: -8px;
  }
  .box-1 {
	  left: 23px;
	  transform: rotate(45deg);
  }
  .box-2 {
	  left: 32px;
	  transform: rotate(63deg);
  }
  .cart-icon {
	  left: 15px;
	  position: absolute;
	  top: 8px;
  }
  .tick {
	  background: #146230;
	  border-radius: 50%;
	  position: absolute;
	  left: 28px;
	  transform: scale(0);
	  top: 5px;
	  z-index: 2;
  }
  @keyframes grow {
	  0% {
		  transform: scale(0);
	 }
	  50% {
		  transform: scale(1.2);
	 }
	  100% {
		  transform: scale(1);
	 }
  }
  @keyframes drop {
	  0% {
		  transform: translateY(0px);
	 }
	  100% {
		  transform: translateY(1px);
	 }
  }
  
