.cart-button-comic .fa-shopping-cart {
	position: absolute;
	z-index: 2;
	top: 50%;
	left: -10%;
	font-size: 2em;
	transform: translate(-50%,-50%);
	outline: none;
}
.cart-button-comic .fa-box {
	position: absolute;
	z-index: 3;
	top: -20%;
	left: 52%;
	font-size: 1.2em;
	transform: translate(-50%,-50%);
	outline: none;
}
.cart-button-comic span {
	position: absolute;
	z-index: 3;
	left: 50%;
	top: 50%;
	font-size: 1.2em;
	color: #fff;
	transform: translate(-50%,-50%);
	outline: none;
}
.cart-button-comic span.add-to-cart {
	opacity: 1;
}
.cart-button-comic span.added {
	opacity: 0;
}

.cart-button-comic.clicked .fa-shopping-cart {
	animation: cart 1.5s ease-in-out forwards;
	outline: none;
}
.cart-button-comic.clicked .fa-box {
	animation: box 1.5s ease-in-out forwards;
	outline: none;
}
.cart-button-comic.clicked span.add-to-cart {
	animation: txt1 1.5s ease-in-out forwards;
	outline: none;
}
.cart-button-comic.clicked span.added {
	animation: txt2 1.5s ease-in-out forwards;
	outline: none;
}
