.button-popup {
	cursor: pointer;
	transition: all 0.3s ease-out;
  }
  
  .overlay-popup {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0, 0, 0, 0.7);
	transition: opacity 500ms;
	visibility: hidden;
	opacity: 0;
  }
  .overlay-popup:target {
	visibility: visible;
	opacity: 1;
  }
  
  .popup-popup {
	margin: 70px auto;
	padding: 20px;
	background: #fff;
	border-radius: 5px;
	width: 30%;
	position: relative;
	transition: all 5s ease-in-out;
  }
  
  .popup-popup h2 {
	margin-top: 0;
	color: #333;
	font-family: Tahoma, Arial, sans-serif;
  }
  .popup-popup .close-popup {
	position: absolute;
	top: 20px;
	right: 30px;
	transition: all 200ms;
	font-size: 30px;
	font-weight: bold;
	text-decoration: none;
	color: #333;
  }
  .popup-popup .close-popup:hover {
	color: #06D85F;
  }
  .popup-popup .content-popup {
	max-height: 30%;
	overflow: auto;
  }
  
  @media screen and (max-width: 700px){
	.box-popup{
	  width: 70%;
	}
	.popup-popup{
	  width: 70%;
	}
  }
